import SingletonComponent from "../components/SingletonComponent";
import { TransformComponent } from "../components/TransformComponent";
import { SystemType } from "../Config";
import { EEntity } from "../Entity";
import { ESystem } from "../System";
import { World } from "../World";

export default class InputSystem implements ESystem {
    world: World;
    type: SystemType = SystemType.Input;

    onUpdate(): void {
        let singleton = this.world.getSingletonEntityComponent(SingletonComponent);
        if (singleton.pressSpace) {
            this.world.forEach([TransformComponent], ((entity: EEntity, transform: TransformComponent) => {
                transform.isMove = !transform.isMove;
                transform.isRotate = !transform.isRotate;
            }));

            singleton.pressSpace = false;
        }
    }

}